import React, { Component } from 'react';

// Style
import '../../style/home/section.css';

class Section extends Component {
    render() {
        return (
            <div className={"section" + ((this.props.reverse)?" reverse":"") + ((this.props.full)?" full":"")}>

                <div className="section-text">
                    
                    <h2>{(!this.props.full) && this.props.title}</h2>

                    <hr />

                    <p>{(!this.props.full) && this.props.description}</p>

                    
                </div>

                <div id={this.props.id} className="section-image"></div>
                
            </div>
        );
    }
}

export default Section;