import React, { Component } from 'react';
import { FaYoutube, FaLinkedin, FaTwitter, FaFacebook, FaInstagramSquare } from 'react-icons/fa';

// Style
import '../style/footer.css';

class Footer extends Component {
    render() {
        return (
            <div id="footer">

                <h2>FOOD</h2>

                <div id="footer-socials">
                    <FaTwitter size={30} color="#000000" />
                    <FaFacebook size={30} color="#000000" />
                    <FaInstagramSquare size={30} color="#000000" />
                    <FaYoutube size={30} color="#000000" />
                    <FaLinkedin size={30} color="#000000" />
                </div>

            </div>
        );
    }
}

export default Footer;